                        strokeColor: '#3399ff',
                        fillColor: 'rgba(51,153,255,0.2)',
                        zIndex: 10
                    }
                ]
            });
        }, 1000); // wcharts

        this.setData({
            imageWidth: uni.getSystemInfoSync().windowWidth
        }); //计算屏幕宽度比列

        windowW = this.imageWidth / 375;
        new wxCharts({
            canvasId: 'columnCanvas',
            type: 'column',
            animation: true,
            categories: [2010, 2012, 2014, 2016, 2018, 2019],
            series: [
                {
                    name: '增长值',
                    data: [17438, 20559, 24068, 28068, 23280, 26800],
                    format: function (val, name) {
                        return val + '亿';
                    }
                }
            ],
            xAxis: {
                disableGrid: false,
                type: 'calibration'
            },
            extra: {
                column: {
                    width: 15
                }
            },
            width: 360 * windowW,
            height: 250 * windowW
        });
    },
    /**
     * 生命周期函数--监听页面初次渲染完成
     */
    onReady: function () {},
    /**
     * 生命周期函数--监听页面显示
     */
    onShow: function () {},
    /**
     * 生命周期函数--监听页面隐藏
     */
    onHide: function () {},
    /**
     * 生命周期函数--监听页面卸载
     */
    onUnload: function () {},
    /**
     * 页面相关事件处理函数--监听用户下拉动作
     */
    onPullDownRefresh: function () {},
    /**
     * 页面上拉触底事件的处理函数
     */
    onReachBottom: function () {},
    /**
     * 用户点击右上角分享
     */
    onShareAppMessage: function () {},
    methods: {
        changTabs: function (e) {
            this.setData({
                selected: e.currentTarget.dataset.index
            });
        },

        handleRegionchange: function (ev) {
            this.mapCtx = uni.createMapContext('myMap');
            this.mapCtx.getCenterLocation({
                success(res) {
                    console.log(res.longitude, '经度');
                    console.log(res.latitude, '纬度');
                }
            }); // console.log(this.mapCtx.getCenterLocation);
        },

        /**
         * marker点击事件
         */
        markertap(e) {
            console.log(e);
        },

        //   标签选中效果
        selectIndex: function (e) {
            this.setData({
                tabIndex: e.currentTarget.dataset.index
            });
        },

        //   标签选中效果
        selectIndex2: function (e) {
            this.setData({
                tabIndex2: e.currentTarget.dataset.index
            });
        },

        // 选择器
        bindPickerChange: function (e) {
            console.log('picker发送选择改变，携带值为', e.detail.value);
            this.setData({
                index: e.detail.value
            });
        },

        selectTag: function (e) {
            console.log(e);
            this.setData({
                tagSelected: e.currentTarget.dataset.index
            });
        },

        // 展开多选
        downText: function (e) {
            console.log(e.currentTarget.dataset.index);
            this.setData({
                down: e.currentTarget.dataset.index
            });
        }
    }
};
</script>
